
html{
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: Microsoft YaHei;
}
.router-link-active {
  color: #5B94DC !important;
  background-color: #fff !important;
}
#app {
  overflow: hidden;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
#app .main-header {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  /*-webkit-box-shadow: 0 2px 8px #f0f1f2;
  box-shadow: 0 2px 8px #f0f1f2;*/
  border-bottom: 1px solid #e8e8e8;
  position: relative;
  z-index: 10;
  height: 65px;
  color: #444;
}
#app .main-header .main-header-logo {
  margin-left: 40px;
  width: 45px;
  display: inline-block;
  height: 100%;
}
#app .main-header .main-header-logo img {
  width: 50px;
  position: relative;
  top: 8px;
}
#app .main-header .main-header-title {
  height: 65px;
  line-height: 65px;
  font-size: 25px;
  display: inline-block;
  font-weight: bold;
}
.main-header-version{
  display: inline-block;
  font-size: 14px;
  margin-left: 5px;
  color: #999;
}
#app .main-header .main-header-nav {
  margin: 0;
  padding: 0;
  float: right;
  height: 100%;
  background: transparent;
  margin-right: 160px;
}
#app .main-header .main-header-nav li {
  list-style: none;
  color: #314659;
  height: 100%;
  line-height: 65px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  min-width: 72px;
  border-top: 2px solid transparent;
  text-align: center;
  cursor: pointer;
  font-size: 15px;
  float: left;
  border-bottom: 3px solid #fff;
  -webkit-transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1),
    border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1),
    border-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
#app .main-header .main-header-nav li.active {
  color: #5B94DC;
  border-bottom: 1px solid #5B94DC;
}
#app .main- {
  flex: 1;
  position: relative;
  /*padding: 40px 0 0;*/
  margin-left: 0;
  margin-right: 0;
  height: auto;
  zoom: 1;
  display: flex;
  flex-direction: row;
}
#app .main- .main--left {
  padding: 25px 0;
  border-right: 1px solid #e8e8e8;
  /*height: calc(100vh - 65px);*/
  overflow-y: scroll;
  float: left;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
}
#app .main- .main--left ul {
  width: 250px;
  margin: 0;
  padding: 0;
}
#app .main- .main--left ul li {
  margin: 0;
  list-style: none;
  padding: 0;
  font-size: 14px;
  color: #455a64;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
#app .main- .main--left ul .group-title {
  font-size: 16px;
  height: 50px;
  line-height: 50px;
  padding: 0 0 0 50px;
  font-weight: 700;
}
#app .main- .main--left ul li a {
  font-size: 14px;
  color: #455a64;
  line-height: 40px;
  height: 40px;
  margin: 0;
  font-weight: 300;
  padding: 0 0 0 65px;
  text-decoration: none;
  display: block;
  position: relative;
  -webkit-transition: 0.15s ease-out;
  transition: 0.15s ease-out;
  /*border-right: 1px solid #e8e8e8;*/
}
#app .main- .main--left ul li a:hover {
  color: #5B94DC;
}
#app .main- .main--left ul li.main--link-group {
  height: auto;
}
#app .main- .main--left ul li.main--link-group a:hover {
  color: #333;
  cursor: default;
}
#app .main- .main--left ul li.main--link-group ul a {
  display: block;
  height: 40px;
  color: #444;
  line-height: 40px;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: 400;
}
#app .main- .main--left ul li.main--link-group ul a:hover {
  color: #5B94DC;
  cursor: pointer;
}
#app .main- .main--right {
  flex: 1;
  overflow-y: scroll;
  padding: 40px 84px 140px 64px;
}
#app .main- .main--right a {
  color: #5B94DC;
  text-decoration: none;
}
.markdown h1 {
  color: #0d1a26;
  font-weight: 500;
  margin-bottom: 20px;
  margin-top: 8px;
  font-size: 30px;
  line-height: 38px;
}
.markdown h2,
.markdown h3,
.markdown h4,
.markdown h5,
.markdown h6 {
  font-family: Lato;
  margin: 1.6em 0 0.6em;
  font-weight: 500;
  clear: both;
}
.markdown h3 {
  font-size: 18px;
}
.markdown h4 {
  font-size: 16px;
}
.markdown h5 {
  font-size: 14px;
}
.markdown h6 {
  font-size: 12px;
}
.markdown p,
.markdown pre {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
.markdown ul > li > p,
.markdown ol > li > p {
  margin: 0.2em 0;
}
.markdown ul {
  padding: 0;
  margin: 0;
}
.markdown ul > li {
  list-style-type: circle;
  margin-left: 20px;
  padding-left: 4px;
  padding-top: 15px;
}
.markdown ol > li {
  list-style-type: decimal;
  margin-left: 20px;
  padding-left: 4px;
  padding-top: 15px;
}
.markdown > table {
  border-collapse: collapse;
  border-spacing: 0;
  empty-cells: show;
  border: 1px solid #ebedf0;
  width: 100%;
  margin: 8px 0 16px;
}
.markdown > table th,
.markdown > table td {
  color: #314659;
  border: 1px solid #ebedf0;
  text-align: left;
  padding: 10px 15px;
}
.markdown > table th {
  white-space: nowrap;
  color: #5c6b77;
  font-weight: 500;
  background: rgba(0, 0, 0, 0.02);
}
.markdown strong,
.markdown b {
  font-weight: 500;
}
.markdown .sh-markdown-permalink {
  opacity: 0;
}
.markdown h1:hover .sh-markdown-permalink,
.markdown h2:hover .sh-markdown-permalink,
.markdown h3:hover .sh-markdown-permalink,
.markdown h4:hover .sh-markdown-permalink,
.markdown h5:hover .sh-markdown-permalink,
.markdown h6:hover .sh-markdown-permalink {
  opacity: 1;
  display: inline-block;
}
.markdown > br,
.markdown p > br {
  clear: both;
}
.markdown blockquote {
  font-size: 90%;
  color: #0d1a26;
  border-left: 4px solid #ebedf0;
  padding-left: 0.8em;
  margin: 1em 0;
}
.markdown blockquote p {
  margin: 0;
}
.markdown hr {
  height: 1px;
  border: 0;
  background: #ebedf0;
  clear: both;
}
.markdown code {
  font-family: Microsoft YaHei;
  margin: 0 1px;
  background: #f2f4f5;
  padding: 0.2em 0.5em;
  border-radius: 3px;
  font-size: 0.9em;
  border: 1px solid #eee;
}
